* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: $font-main;
  font-weight: 400;
  font-size: 16px;
}

html {
  scroll-behavior: smooth;

  &:has(.menu:target) {
    overflow: hidden;
  }
}

a {
  text-decoration: none;
}

ul,
ol {
  list-style: none;
}

.text-orange {
  color: #cd4d31;
}
